@import '../../../assets/scss/colors.scss';

.Shop {
    padding-top: 5rem;
    background-color: $main;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    .Filters {
        position: fixed;
        color: $second;
        text-align: center;
        width: 100%;
        margin: 0;
        padding: 2rem 0;
        bottom: 0;
        left: 0;
        color: $second, 2%;
    }

    .PerPage {
        margin: 5rem 0;
    }

    .ProductsHolder {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        width: 100%;
        padding: 5rem 0;

        .Product {
            height: 35rem;
            width: 100%;
            max-width: 25rem;
            background-color: darken($main, 2%);
            margin-bottom: 3rem;

            .First {
                height: 45%;
                background: url('../../../assets/photos/prod.png') no-repeat;
                background-position: center center;
                background-size: 75%;
            }

            .Second {
                height: 55%;
                padding: 0 2rem;

                h1 {
                    color: $second;
                }

                span {
                    color: $second;
                }

                .Bottom {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    color: $second;

                    h3 {
                        color: $second;
                    }

                    button {
                        color: $second;
                        background-color: transparent;
                        font-family: inherit;
                        font-weight: bold;
                        border: 3px solid $second;
                        border-radius: 10px;
                        padding: 0.5rem 1rem;
                    }
                }
            }
        }
    }
}